/* SCSS GLOBALS */
/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important; }
  .screen-reader-text:focus {
    background-color: #eee;
    clip: auto !important;
    clip-path: none;
    color: #444;
    display: block;
    font-size: 1em;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto; }

.alignright {
  float: right;
  margin: 5px 0 20px 20px; }

.alignleft {
  float: left;
  margin: 5px 20px 20px 0; }

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px; }

a img.alignnone {
  margin: 5px 20px 20px 0; }

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0; }

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center; }

.wp-caption.alignnone {
  margin: 5px 20px 20px 0; }

.wp-caption.alignleft {
  margin: 5px 20px 20px 0; }

.wp-caption.alignright {
  margin: 5px 0 20px 20px; }

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto; }

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px; }

.alignfull,
.is-style-full-browser-width,
.alignwide,
.full-width {
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw; }

.alignwide {
  margin-left: calc(25% - 25vw);
  margin-right: calc(25% - 25vw);
  width: 75vw;
  min-width: 100%; }

/**
 * General styles applied to all content
 */
/*
html {
	min-height: 120vh;
}
*/
html, body {
  background-color: var(--cmls_base-color-site-background);
  color: var(--cmls_base-color-content-foreground);
  font-family: var(--cmls_base-font-font_family);
  font-size: var(--cmls_base-font-base_size);
  line-height: var(--cmls_base-font-base_lineheight); }
  @media (max-width: 820px) {
    html, body {
      font-size: calc(var(--cmls_base-font-base_size) * 0.95); } }
  @media (max-width: 640px) {
    html, body {
      font-size: calc(var(--cmls_base-font-base_size) * 0.9); } }

h1, h2, h3, h4, h5, h6 {
  display: block;
  font-size: 1em;
  font-weight: bold;
  line-height: 1.2;
  margin: 0 0 .5em;
  padding: 0; }

h1 {
  font-size: 2.5em; }

h2 {
  font-size: 2em; }

h3 {
  font-size: 1.75em; }

h4 {
  font-size: 1.5em; }

h6 {
  font-size: 1.25em; }

p {
  margin-top: 0; }

a {
  color: var(--cmls_base-color-brand);
  outline: 0; }
  a:hover, a:focus {
    color: var(--cmls_base-color-highlight); }
  @media (hover: none) {
    a {
      color: var(--cmls_base-color-brand); } }
  a img {
    border: 0; }

figure {
  margin: 0; }
  figure.aligncenter {
    margin: 0 auto; }

img:not([src]) {
  visibility: hidden; }

body > main,
body.editor-styles-wrapper {
  background-color: var(--cmls_base-color-content-background);
  font-size: 1rem; }

body.editor-styles-wrapper {
  font-size: var(--cmls_base-font-base_size); }

.row {
  position: relative;
  overflow: visible; }

.row-container {
  margin: 0 auto;
  max-width: 1200px;
  position: relative;
  overflow-x: visible; }

img[data-credit], .has_image[data-credit], figure.wp-block-image, figure.wp-block-video {
  position: relative; }
  img[data-credit] img, .has_image[data-credit] img, figure.wp-block-image img, figure.wp-block-video img {
    display: inherit; }
  img[data-credit]:after, img[data-credit] figcaption, .has_image[data-credit]:after, .has_image[data-credit] figcaption, figure.wp-block-image:after, figure.wp-block-image figcaption, figure.wp-block-video:after, figure.wp-block-video figcaption {
    content: attr(data-credit);
    background-color: var(--cmls_base-color-brand);
    color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    padding: .75em .9em;
    margin: 0;
    font-size: 0.4rem; }

figure.wp-block-image:after,
figure.wp-block-video:after {
  display: none; }

figure.wp-block-video figcaption {
  bottom: auto;
  top: 0; }

.text-shadow-1 {
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); }

.text-shadow-2 {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); }

.text-shadow-3 {
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.6); }

.body p:last-child {
  margin-bottom: 0; }

.no-link-underline a,
a.no-link-underline {
  text-decoration: none; }

.negative-margin-top-1em {
  margin-top: -1em; }

.negative-margin-top-2em {
  margin-top: -2em; }

figure.circle-mask img,
img.circle-mask {
  border-radius: 100%;
  overflow: hidden; }

